'use client';
import "@fortawesome/fontawesome-free/css/all.min.css";
import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { ChevronLeft, ChevronDown } from 'lucide-react';
import { useApi } from '../context/ApiContext'; // عدّل المسار حسب مشروعك

type Service = { id: number; name: string };
type Props = { services: Service[]; cities: string[] };

export default function StartProjectForm({ services, cities }: Props) {
  const router = useRouter();
  const [step, setStep] = useState(1);
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [form, setForm] = useState({
    name: '',
    serviceId: '',
    apartmentType: '',
    apartmentSize: '',
    preferredStyle: '',
    materialQuality: '',
    minBudget: '',
    maxBudget: '',
    city: '',
    details: '',
    images: [] as File[],
  });
  const { baseUrl } = useApi();

  const handleNext = async () => {
    setErrorMessage('');
    // تحقق من الحقول حسب الخطوة
    if (step === 1 && (!form.name || !form.serviceId )) {
      setErrorMessage('Please complete all required fields in Step 1.');
      return;
    }
    if (step === 2 && (!form.minBudget || !form.maxBudget)) {
      setErrorMessage('Please complete all required fields in Step 2.');
      return;
    }
    

    if (step < 3) {
      setStep(step + 1);
    } else {
      // ارسال النموذج مع اظهار تحميل
      const token = localStorage.getItem('token');
      if (!token) {
        setErrorMessage('Please login first.');
        return;
      }

      setLoading(true);
      const formData = new FormData();
      formData.append('Name', form.name);
      formData.append('ServiceId', form.serviceId);
      formData.append('ApartmentType', form.apartmentType);
      formData.append('ApartmentSize', form.apartmentSize);
      formData.append('PreferredStyle', form.preferredStyle);
      formData.append('MaterialQuality', form.materialQuality);
      formData.append('MinBudget', form.minBudget);
      formData.append('MaxBudget', form.maxBudget);
      formData.append('Details', form.details);
      form.images.forEach((img) => formData.append('ImageFiles', img));

      try {
        const res = await fetch(`${baseUrl}/api/Projects`, {
          method: 'POST',
          headers: {
            Authorization: `Bearer ${token}`,
          },
          body: formData,
        });

        if (!res.ok) {
          const errorText = await res.text();
          throw new Error(errorText || 'Upload failed');
        }

        // تحويل للصفحة بدون alert
        router.push('/projects');
      } catch (err: any) {
        console.error('Error:', err);
        setErrorMessage('Failed to create project. Please try again.');
      } finally {
        setLoading(false);
      }
    }
  };

  const handleBack = () => step > 1 && setStep(step - 1);

  const onImagesChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      setForm((f) => ({ ...f, images: Array.from(e.target.files) }));
    }
  };

  const SelectField = ({
    label,
    value,
    options,
    onChange,
  }: {
    label: string;
    value: string;
    options: string[] | { id: number; name: string }[];
    onChange: (value: string) => void;
  }) => {
    const [open, setOpen] = useState(false);

    return (
      <div className="relative">
        <button
          type="button"
          onClick={() => setOpen(!open)}
          className="w-full border border-gray-300 rounded px-4 py-3 text-sm flex justify-between items-center"
        >
          {value || label} <ChevronDown className="w-4 h-4" />
        </button>
        {open && (
          <ul className="absolute left-0 right-0 bg-white border border-gray-300 mt-1 rounded shadow-md z-20 max-h-60 overflow-y-auto">
            {(options as any[]).map((option, index) => {
              const label = typeof option === 'string' ? option : option.name;
              const val = typeof option === 'string' ? option : option.id.toString();
              return (
                <li
                  key={index}
                  onClick={() => {
                    onChange(val);
                    setOpen(false);
                  }}
                  className="px-4 py-2 text-sm hover:bg-gray-100 cursor-pointer"
                >
                  {label}
                </li>
              );
            })}
          </ul>
        )}
      </div>
    );
  };

  const spaceTypes = ['Apartment', 'Villa', 'Studio', 'Office', 'Duplex'];
  const finishStyles = ['Modern', 'Classic', 'Minimalist', 'Industrial', 'Bohemian'];
  const qualityLevels = ['High Quality', 'Medium Quality', 'Economy Quality'];

  return (
    <div className="flex justify-center items-center min-h-screen bg-gray-100 px-4">
      <div className="bg-white rounded-xl shadow-md w-full max-w-md p-6 relative">
        {/* Header */}
        <div className="flex justify-between items-center mb-6">
          <button onClick={handleBack} disabled={step === 1}>
            <ChevronLeft className={`w-5 h-5 ${step === 1 ? 'text-gray-300' : 'text-black'}`} />
          </button>
          <h2 className="text-center font-semibold text-sm flex-1">Start a New Project</h2>
          <button onClick={() => window.history.back()}>✕</button>
        </div>

        {/* Progress */}
        <div className="flex justify-between mb-6">
          {[1, 2, 3].map((i) => (
            <div key={i} className="flex-1 text-center">
              <div className={`h-1 mb-2 rounded-full ${step >= i ? 'bg-green-500' : 'bg-gray-200'}`} />
            </div>
          ))}
        </div>

        {/* Step Content */}
        {step === 1 && (
          <div className="space-y-4">
            <input
              type="text"
              placeholder="Project name"
              value={form.name}
              onChange={(e) => setForm((f) => ({ ...f, name: e.target.value }))}
              className="w-full border border-gray-300 rounded px-4 py-2 text-sm"
            />
            <SelectField
              label="Select Service"
              value={services.find((s) => s.id.toString() === form.serviceId)?.name || ''}
              options={services}
              onChange={(val) => setForm((f) => ({ ...f, serviceId: val }))}
            />
            <SelectField
              label="Select Space Type"
              value={form.apartmentType}
              options={spaceTypes}
              onChange={(val) => setForm((f) => ({ ...f, apartmentType: val }))}
            />
            <input
              type="text"
              placeholder="Apartment Size"
              value={form.apartmentSize}
              onChange={(e) => setForm((f) => ({ ...f, apartmentSize: e.target.value }))}
              className="w-full border border-gray-300 rounded px-4 py-2 text-sm"
            />
          </div>
        )}
        {step === 2 && (
          <div className="space-y-4">
            <SelectField
              label="Select Finish Style"
              value={form.preferredStyle}
              options={finishStyles}
              onChange={(val) => setForm((f) => ({ ...f, preferredStyle: val }))}
            />
            <SelectField
              label="Select Quality"
              value={form.materialQuality}
              options={qualityLevels}
              onChange={(val) => setForm((f) => ({ ...f, materialQuality: val }))}
            />
            <input
              type="number"
              placeholder="Min Budget"
              value={form.minBudget}
              onChange={(e) => setForm((f) => ({ ...f, minBudget: e.target.value }))}
              className="w-full border border-gray-300 rounded px-4 py-2 text-sm"
            />
            <input
              type="number"
              placeholder="Max Budget"
              value={form.maxBudget}
              onChange={(e) => setForm((f) => ({ ...f, maxBudget: e.target.value }))}
              className="w-full border border-gray-300 rounded px-4 py-2 text-sm"
            />
          </div>
        )}
        {step === 3 && (
          <div className="space-y-4">
            <SelectField
              label="Select City"
              value={form.city}
              options={cities}
              onChange={(val) => setForm((f) => ({ ...f, city: val }))}
            />
            <textarea
              placeholder="Additional notes..."
              value={form.details}
              onChange={(e) => setForm((f) => ({ ...f, details: e.target.value }))}
              className="w-full border border-gray-300 rounded px-4 py-2 text-sm h-24 resize-none"
            />
            <input type="file" multiple accept="image/*" onChange={onImagesChange} />
          </div>
        )}

        {/* Next / Finish Button */}
        <button
          onClick={handleNext}
          disabled={loading}
          className={`w-full py-3 rounded mt-6 text-white ${loading ? 'bg-gray-400 cursor-not-allowed' : 'bg-green-500'}`}
        >
          {loading ? <i className="fa fa-spinner fa-spin"></i> : step < 3 ? 'Next' : 'Finish'}
        </button>

        {/* Error message */}
        {errorMessage && (
          <p className="mt-3 text-center text-red-600 text-sm">{errorMessage}</p>
        )}
      </div>
    </div>
  );
}
